import Vuex from 'vuex'
import Vue from 'vue' 
import persistedState from 'vuex-persistedstate'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    token:null,
    isUserLogin:false,
    username:null,
  },
  getters:{//Getterstate
    doneTools:state =>{
     return state.token//
    },
    userLogin:state =>{
      return state.isUserLogin//
    },
    userName: state => {
      return state.username //
    }
  },
  mutations: {
    setToken(state, token){ //token
        state.token = token
        if(token){
            state.isUserLogin = true
        }else{
            state.isUserLogin = false
        }
    },
    setUsername(state, username){
      state.username = username
    }
  },
  actions: {
    setToken({commit}, token){  commit('setToken', token) },
    setUsername({commit}, username){  commit('setUsername', username) },

  },
  plugins: [persistedState()] //localStorage 
})


