<template>
    <div>
    <el-row style="text-align:left">
      <el-button-group>
        <el-button type="success" :disabled="start_status" size="small" @click="containerOpt('start')"><i class="el-icon-video-play"></i> </el-button>
        <el-button type="danger" :disabled="stop_status" size="small" @click="containerOpt('stop')"><i class="el-icon-video-pause"></i> </el-button>
        <el-button type="primary" :disabled="restart_status" size="small" @click="containerOpt('restart')"><i class="el-icon-refresh"></i> </el-button>
        <el-button type="primary" :disabled="pause_status" size="small" @click="containerOpt('pause')"><i class="el-icon-video-pause"></i> </el-button>
        <el-button type="primary" :disabled="resume_status" size="small" @click="containerOpt('unpause')"><i class="el-icon-video-play"></i> </el-button>
        <el-button type="danger" :disabled="remove_status" size="small" @click="containerOpt('remove')"><i class="el-icon-delete"></i> </el-button>
      </el-button-group>
      <el-button style="margin-left: 10px" :disabled="create_status" type="primary" size="small" @click="createContainer" ><i class="el-icon-plus"></i> </el-button>
    </el-row>
    <el-row>
      <el-table
      :row-key="getRowKey"
      ref=multipleTable
      v-loading="loading"
      :data="tableData.slice((currentPage-1)*pageSize,currentPage*pageSize)"
      :v-model="tableData"
      stripe
      highlight-current-row
      style="width: 100%"
      tooltip-effect="dark"
      @selection-change="handleSelectionChange"
      
      >
        <el-table-column
          type="selection"
          width="55"  :reserve-selection="reserveSelection">
        </el-table-column>
         <el-table-column prop="Id" label="ID" v-if="false"></el-table-column>
        <el-table-column prop="Name" label=""></el-table-column>
        <el-table-column   prop="State" label="">
          <template slot-scope="tableData">
            <el-tag v-if="tableData.row.State=='paused'" type="warning" size="small"></el-tag>
            <el-tag v-if="tableData.row.State=='exited'" type="danger" size="small"></el-tag>
            <el-tag v-if="tableData.row.State=='running'" type="success" size="small"></el-tag>
            <el-tag v-if="tableData.row.State=='created'" type="success" size="small"></el-tag>
            <el-tooltip class="item" effect="dark" content="" placement="top-start">
              <el-link icon="el-icon-view" @click.prevent="inspectContainer(tableData.row)"></el-link>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="top-start">
               <el-link icon="el-icon-s-marketing" @click.prevent="containerState(tableData.row)"></el-link>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column  prop="Image" label=""></el-table-column>
        <el-table-column prop="Created" label=""></el-table-column>
        <el-table-column prop="IPAddress" label="IP"></el-table-column>
      </el-table>
    </el-row>
    <el-row>
    <div class="block">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="pageSizes"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="tableData.length"
        v-if="paginationShow"
        >
      </el-pagination>
    </div>
    </el-row>
  </div>
</template>


<script>
  export default {
    data() {
      return {
        tableData:[],
        multipleSelection: [],
        start_status:true,
        stop_status:true,
        restart_status:true,
        resume_status:true,
        remove_status:true,
        pause_status:true,
        containers_Ids:[],
        create_status:false,
        currentPage:1,
        pageSizes:[3,5,10],
        pageSize:3,
        loading:true,
        multipleSelection: [],
        paginationShow:true, 
        reserveSelection:true
      }
    },
    methods: {
      async setTable(){
        this.loading = true //
        let tempData = await this.$api.container.getContainers()
        if(tempData !== null){
          this.tableData = tempData
        }else{
          this.$message({type:'error', message:''})
        }
        this.loading = false
      },

      changeAllstatus(status){
        this.start_status = this.stop_status = this.restart_status = this.resume_status = this.remove_status = this.pause_status = status
      },

      changeButtonStatus(State){
        this.changeAllstatus(true)
        State.forEach(item =>{
          if(item == "exited"){ //
            this.start_status = this.restart_status = this.remove_status = false
          }else if(item == "running"){
            this.pause_status = this.stop_status = this.restart_status = this.remove_status = false
         }else if(item == "paused"){
            this.resume_status = this.stop_status = this.restart_status = this.remove_status = false 
         }else if(item == "created"){
           this.start_status = this.restart_status = this.remove_status = false
         }
        })
       
      },
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
        let status = []
        this.containers_Ids = []
        val.forEach(item => {
          status.push(item.State)
          this.containers_Ids.push(item.Id)
        })
        this.changeButtonStatus(status)
      },
      getRowKey(row){
        return row.Id 
      },

      // currentPagepagesizedata
      handleSizeChange: function (size) {
              this.pageSize = size;
      },
      handleCurrentChange: function(currentPage){
              this.currentPage = currentPage;
      },

      //
      async optQuery(opt){
        this.loading = true //
        //
        this.paginationShow = false 
        //
        this.create_status= this.start_status = this.stop_status = this.restart_status = this.resume_status = this.remove_status = this.pause_status = true
        //
        let res = false
        for(let i=0 ;i < this.containers_Ids.length; i++){
           res = await this.$api.container.containerOpt(this.containers_Ids[i], opt)
           if(!res) break;
        }
        this.setTable() //
        //
        this.paginationShow = true
        //
        this.multipleSelection = []
        this.$refs.multipleTable.clearSelection();
        //
        this.create_status = false 
        //
        return res
     },

     //
     allowOpt(opt){
       switch (opt) {
         case "start":
           for(var i = 0; i< this.multipleSelection.length; i++){
             let item = this.multipleSelection[i]
             if(item.State != "exited" && item.State != "created" ){
                this.$message({type:'error', message:item.Name + ''})
                return false;
              }
           }
           break;
       case "stop":
            for(var i = 0; i< this.multipleSelection.length; i++){
              let item = this.multipleSelection[i]
              if(item.State  != "running" ){
                this.$message({type:'error', message:item.Name + ''})
                return false;
              }
            }
           break;
        case "pause":
            for(var i = 0; i< this.multipleSelection.length; i++){
              let item = this.multipleSelection[i]
              if(item.State  != "running" ){
                this.$message({type:'error', message:item.Name + ''})
                return false;
              }
            }
           break;
        case "unpause":
           for(var i = 0; i< this.multipleSelection.length; i++){
              let item = this.multipleSelection[i]
              if(item.State  != "paused" ){
                this.$message({type:'error', message:item.Name + ''})
                return false;
              }
            }
           break;
        case "restart":
            for(var i = 0; i< this.multipleSelection.length; i++){
              let item = this.multipleSelection[i]
              if(item.State  != "running" && item.State != "exited" && item.State != "created"){
                this.$message({type:'error', message:item.Name + ''})
                return false;
              }
            }
           break;
        case "remove":
            for(var i = 0; i< this.multipleSelection.length; i++){
              let item = this.multipleSelection[i]
              if(item.State  === "running"){
                  return false;
              }
            }
            break;
         default:
           break;
       }
       return true
     },

     //
     containerOpt(opt){
       if(opt === "remove"){
         let res = false
         let successInfo = "!"
         let errorInfo = "!"
         if(!this.allowOpt(opt)){ //
            this.$confirm(',?', '', {confirmButtonText: '', cancelButtonText: '',type: 'warning'})
                .then( async () => {
                  res = await this.optQuery(opt)
                  if(res){this.$message({type: 'success',message: successInfo});
                  }else{this.$message({type: 'error',message: errorInfo});}   
                }).catch(() => {this.$message({type:'info', message:""})});
         }else{
            this.$confirm('', '', {confirmButtonText: '', cancelButtonText: '',type: 'warning'})
                .then( async () => {
                  res = await this.optQuery(opt)
                  if(res){this.$message({type: 'success',message: successInfo});
                  }else{this.$message({type: 'error',message: errorInfo});}   
                  }).catch((error) => { console.log(error); this.$message({type:'info', message:""})});
         }
       }else if(this.allowOpt(opt)){ //
         let info = ""
         switch (opt) {
           case "start": info = "";break;
           case "restart": info = "";break;
           case "stop": info = "";break;
           case "pause": info = "";break;
           case "unpause": info = "";break;
           case "remove": info = "";break;
           default:
             break;
         }
         //
        this.$confirm('' + info + '?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
          }).then( async () => {
            let res = await this.optQuery(opt)
            if(res){this.$message({type: 'success',message: info+'!'});
            }else{this.$message({type: 'error',message: info+'!'});}   
            
          }).catch((error) => {
            console.log(error)
            this.$message({ type: 'info',message: ''});          
          });
      }
        
     },

      inspectContainer(row){
        this.$router.push({path:"/home/inspectContainer", query:{id:row.Id}})
      },
      containerState(row){
        if(row.State != "running")
        {
            this.$message({ type: 'info',message: ''}); 
        }else{
          this.$router.push({path:"/home/ContainerState", query:{id:row.Id}})
        }
      },
      createContainer(){
        this.$router.push("/home/createContainer")
      },
    },
    activated(){
      
      this.setTable()
    },
   
  }
</script>

<style scoped>
  .el-table-column{
    height: 8px;
  }
  .el-pagination{
    text-align: right;
  }
  .el-row {
    margin-bottom: 20px;
  }
  .item {
      margin: 4px;
  }
</style>


